@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  z-index: 1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.center{
    text-align: center;
}

.inline{
    display: inline-block;
}

.title{
    color:rgb(0, 81, 255);
    font-weight: bold;
}
body{
  background-color: lightblue;
}
@-ms-viewport{
  max-zoom: 300%;

}
@viewport{
  max-zoom: 300%;

}
.bar{
  border:1px solid black;
  float: left;
}

.chart{
  border-bottom: 1px solid black;
  border-left: 1px solid black;
  overflow: hidden;
}
.chart >*{
  background: rgb(0, 102, 255);
}
.frecuences{
  float: left;
}
.horizontalLines{
  border: 0.2px solid rgba(0, 0, 255, 0.575);
  position: relative;
  z-index: -1;
  float:right;
}

section{
  padding-top: 10px;
  overflow: visible;
}

.label{
  width: 50px;
  transform: rotate(-90deg);
  font-size: small;

  white-space: nowrap;
}

.red{
  border:1px solid red;
}
.title{
  color:rgb(0, 81, 255);
  font-weight: bold;
}
button{
    box-shadow: 2px 2px rgba(0, 0, 0, 0.171);
    overflow: hidden;
    white-space: nowrap;
}

.cost {
    display:inline;
}

.otherType{

    width: 150px;
}

.categories >*{
    height: 30px;
    width: 100px;
}

.inlineTable{
    display: inline-table  ;
}

.notification{
    color: red;
    font-family: 'Raleway', sans-serif;
    font-size: small;
    margin: 0;

}

.left{
 margin-left : 8px;
 text-align: left;
}

.loadingPurchase{
    color:red;
    font-family: 'Raleway', sans-serif;
    font-size: small;
}
.selectedCategory{
    background:rgba(0, 110, 255, 0.849);
}
